
import { defineComponent } from "vue";
import { CommonCodeDetail, COM21100131_RES, COM21100121_RES, COM21100112_RES } from "@/shared/type/com211001sc01.type";
import { modalController } from "@ionic/vue";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import { screenMode } from "@/shared/type/Approval.type";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";

export default defineComponent({
    name: "com211001sc01",
    components: {
        BreadCrumb
    },

    data() {
        const commonCodeDetail = {
            valueCodeList: [
                {
                    businessCommonStatusCode: "",
                    standardCodeDomainName: "",
                    standardCodeDisplayOrder: "",
                    standardCodeValue: "",
                    standardCodeDetailLanguageList: [
                        {
                            languageCode: "",
                            standardCodeValueDesc: "",
                            businessCommonStatusCode: "",
                        },
                    ],
                    standardCodeDomainNo: "",
                },
            ],
            codeDataTypeContent: "VARCHAR",
            standardCodeKorDomainNo: "",
            standardCodeDomainDesc: "",
            standardCodeDomainName: "",
            standardCodeDomainNo: "",
            businessCommonStatusCode: "01",
            codeDataLengthContent: "",
        } as CommonCodeDetail
        const statusCode = [
            { key: '01', value: 'Active' },
            { key: '02', value: 'Inactive' },
        ] as { key: string, value: string }[]
        const dataType = [
            { key: "NUMERIC", value: "NUMERIC" },
            { key: "VARCHAR", value: "VARCHAR" },
        ] as { key: string, value: string }[]
        const isEditable = true as boolean
        const isInValidInput = false as boolean

        return {
            isEditable,
            commonCodeDetail,
            isInValidInput,
            statusCode,
            dataType,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any
        }
    },

    async mounted() {
        if (this.approvalType === '') {
            if (this.$route.query.type === "edit") {
                this.retrieveCommonCodeDetail();
            } else {
                this.isEditable = false;
            }
        } else {
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                this.commonCodeDetail = authDetailParsed
            })
        }
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },

        covertCodeValue() {
            return (statusCode: string, statusValue: string) => `${statusCode}` + ' : ' + `${statusValue}`;
        },

        btnCancel() {
            if (!this.isEditable) {
                return true
            }
            return false
        },

        btnList() {
            if (this.$route.query.type == 'edit' && this.isEditable) {
                return true
            }
            return false
        },

        btnEdit() {
            if (this.$route.query.type == 'edit' && this.isEditable) {
                return true
            }
            return false
        }
    },

    methods: {
        async retrieveCommonCodeDetail() {
            const reqBody = { standardCodeDomainName: this.$route.query.standardCodeDomainName }
            const res = await this.$request.getRequest("COM21100112", reqBody, false) as COM21100112_RES;
            if (res?.header.result) this.commonCodeDetail = res?.body
        },

        async handleRegister() {
            const reqBody = {
                standardCodeKorDomainNo: this.commonCodeDetail.standardCodeKorDomainNo,
                standardCodeDomainName: this.commonCodeDetail.standardCodeDomainName,
                businessCommonStatusCode: this.commonCodeDetail.businessCommonStatusCode,
                codeDataTypeContent: this.commonCodeDetail.codeDataTypeContent,
                standardCodeDomainNo: this.commonCodeDetail.standardCodeDomainNo,
                codeDataLengthContent: Number(this.commonCodeDetail.codeDataLengthContent),
                standardCodeDomainDesc: this.commonCodeDetail.standardCodeDomainDesc,
            }
            const res = await this.$request.getRequest("COM21100121", reqBody, false) as COM21100121_RES
            if (res?.header.result) {
                this.$dialog.onOpen({
                    content: this.$t("message.register_success"),
                    dialogMode: 'Success',
                    callback: () => { modalController.dismiss() }
                })
            }
        },

        async handleEdit() {
            const reqBody = {
                standardCodeKorDomainNo: this.commonCodeDetail.standardCodeKorDomainNo,
                standardCodeDomainName: this.commonCodeDetail.standardCodeDomainName,
                businessCommonStatusCode: this.commonCodeDetail.businessCommonStatusCode,
                codeDataTypeContent: this.commonCodeDetail.codeDataTypeContent,
                standardCodeDomainNo: this.commonCodeDetail.standardCodeDomainNo,
                codeDataLengthContent: Number(this.commonCodeDetail.codeDataLengthContent),
                standardCodeDomainDesc: this.commonCodeDetail.standardCodeDomainDesc
            }
            const res = await this.$request.getRequest("COM21100131", reqBody, false) as COM21100131_RES
            if (res?.header.result) {
                this.isEditable = true
                this.$dialog.onOpen({
                    content: this.$t("message.update_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        this.retrieveCommonCodeDetail()
                        modalController.dismiss()
                    }
                })
            }
        },

        handleActionClick(actionType: string) {
            switch (actionType) {
                case "edit":
                    this.isEditable = false
                    break;
                case "cancel":
                    this.isInValidInput = false
                    this.$dialog.onOpen({
                        content: this.$route.query.type === 'edit' ? this.$t('message.cancel_without_saving_changes') : this.$t('message.are_you_sure_you_want_to_cancel_registration?'),
                        dialogMode: "Question",
                        callback: () => {
                            if (this.$route.query.type == "edit") {
                                this.retrieveCommonCodeDetail()
                                this.isEditable = true
                            } else {
                                this.$router.push("COM211001SC00")
                            }
                        },
                    });
                    break;
                case "save":
                    if (
                        this.commonCodeDetail.standardCodeDomainName &&
                        this.commonCodeDetail.standardCodeDomainNo &&
                        this.commonCodeDetail.businessCommonStatusCode &&
                        this.commonCodeDetail.codeDataTypeContent &&
                        this.commonCodeDetail.codeDataLengthContent &&
                        this.commonCodeDetail.standardCodeDomainDesc
                    ) {
                        this.$dialog.onOpen({
                            content: this.$t("message.would_you_like_to_save"),
                            dialogMode: "Question",
                            callback: () => {
                                if (this.$route.query.type === "edit") {
                                    this.handleEdit()
                                        .then(() => modalController.dismiss())
                                } else {
                                    this.handleRegister()
                                        .then(() => {
                                            modalController.dismiss()
                                            this.$router.push("COM211001SC00")
                                        })
                                }
                            },
                        });
                    } else {
                        this.isInValidInput = true;
                    }
                    break;
                default:
                    this.$router.push("COM211001SC00");
                    break;
            }
        },

        checkSpace(event: Event) {
            this.$validate.validateSpace(event)
        },

        onKeyDown(event: Event) {
            this.$validate.validateNumber(event)
        },
    },
});
